import type { HttpWeblogInstance } from '@/types/index';
import { BasePlugin } from '../base';
import { addMonitor, removeMonitor } from '../../util';

const defaultOptions = {
    updateInterval: 20 * 1000,
    crashThreshold: 10 * 1000,
};

const monitorPrefix = '@ks.monitor.crash_';
export default class CrashMonitor extends BasePlugin {
    private eventName = 'onpagehide' in window ? 'pagehide' : 'beforeunload';
    static override key = 'crachMonitor';
    private options = {
        ...defaultOptions,
    };
    private updateIntervalId!: number;
    private historyMoniorInfo: any = null;
    monitorId!: string;
    /**
     * 兼容写法，如果在 Weblog 初始化设置 plugins 时，不需要设置 weblog
     * @param weblog 
     */
    constructor(weblog?: HttpWeblogInstance) {
        super();
        if ((weblog as HttpWeblogInstance)?.collect) {
            this.apply(weblog as HttpWeblogInstance);
        }
        addMonitor(window, this.eventName, this.stop);
    }
    override apply(weblog: HttpWeblogInstance) {
        if (!weblog) return;
        this.weblog = weblog;
        this.monitorId = monitorPrefix + this.weblog.sessionId;
        this.start();
    }
    destroy() {
        this.stop();
        removeMonitor(window, this.eventName, this.stop);
    }
    pickHistoryInfo() {
        for (const key in localStorage) {
            if (key.indexOf(monitorPrefix) === 0) {
                try {
                    const historyMoniorString = localStorage.getItem(key);
                    const data = JSON.parse(historyMoniorString as string);
                    this.checkHistoryCrash(data);
                } catch (e) {
                    // nothing
                }
            }
        }
    }
    checkHistoryCrash(data: any) {
        const now = new Date().valueOf();
        if (data && data.time && now - data.time > this.options.crashThreshold) {
            this.weblog.collect('CUSTOM', {
                key: 'crash',
                value: data
            });
        }
    }
    /**
     * 开始定时更新快照
     */
    start() {
        this.pickHistoryInfo();
        this.updateIntervalId = window.setInterval(() => {
            this.update();
        }, this.options.updateInterval);
        this.update();
    }
    /**
     * 更新监控快照
     * @param {*} data 外部追加额外的快照信息
     */
    update() {
        // TODO，非标准api后续可能会需要更新
        if (!window.performance || !window.performance.memory) {
            return;
        }
        // TODO后面看看需要补其他什么信息
        const monitorInfo = {
            time: new Date().valueOf(),
            memory: {
                limit: Math.floor(window.performance.memory.jsHeapSizeLimit / 1024),
                total: Math.floor(window.performance.memory.totalJSHeapSize / 1024),
                used: Math.floor(window.performance.memory.usedJSHeapSize / 1024),
            },
            href: window?.location?.href,
            sessionId: this.weblog?.sessionId
        };
        try {
            localStorage.setItem(this.monitorId, JSON.stringify(monitorInfo));
        } catch (e) {
            // nothing
        }
    }
    /**
     * 停止监控过程
     * 1.在页面正常卸载时自动调用
     * 2.需要手动卸载时手动调用
     */
    stop = () => {
        clearInterval(this.updateIntervalId);
        try {
            localStorage.removeItem(this.monitorId);
        } catch (e) {
            // nothing
        }
    }
}